import React, { Component } from 'react';


function ShiftLumberTr (props) {
  const { record } = props
  const measureClass = 'font-10'
  return (
    <tr>
      <td style={{lineHeight: '15px'}}>
        {record.wood_species === 'larch'  
          ? <span className='d-block color-brown1-light'></span>
          : <span className='d-block color-green1-light'></span>
        }
        {record.lumber.includes('') 
          ? <span className='font-italic'>{record.lumber}</span>
          : <span className=''>{record.lumber}</span>
        }
      </td>
      <td>
        {record.quantity}
        <span className={measureClass}> </span>
      </td>
      <td>{record.volume}<span className={measureClass}> 3</span></td>
      <td>{record.rate}<span className={measureClass}> </span></td>
      <td>{record.total_cash}<span className={measureClass}> </span></td>
    </tr>
  )
}

export function ShiftComponent (props) {
  const { shift, user, deleteShift } = props
  return (
    <div className='card card-style'>
      <div className='content mb-1'>
        <div className='d-flex justify-content-between'>
          <p className='color-black mb-0 font-16'>: {shift.date} {shift.shift_type}</p>
          {user.is_manager && 
            <button className='btn btn-s bg-red1-light mr-2 text-uppercase' 
            onClick={() => deleteShift(shift.id)}>
              </button>
          }
        </div>
        <p className='mb-0 color-black'>
          : 
          {shift.employees.map(emp => <span className='mx-1'>{emp}</span>)}
        </p>
        <p className='color-black mb-0 font-13 d-flex justify-content-between' style={{lineHeight: '15px'}}>
          <span>: <span className='d-block font-600 font-14'>{shift.volume} m3</span></span>
          <span> : <span className='d-block font-600 font-14'>{shift.back_calc_cash} </span></span>
          <span> : <span className='d-block font-600 font-14'>
            {shift.back_calc_cash_per_employee} </span></span>
        </p>
      </div>
      <div className='content mt-0'>
        <table className='table table-sm'>
          <thead>
            <th></th>
            <th>-</th>
            <th></th>
            <th></th>
            <th></th>
          </thead>
          <tbody>
            {shift.lumber_records.map(record => 
              <ShiftLumberTr record={record}/>
            )}
          </tbody>
        </table>
      </div>
    </div>
  )
}

export function ShiftList (props) {
  const { shiftList, user, deleteShift } = props
  return (
    <div className='mt-2'>
      <h4 className='my-3 text-center'>  ({shiftList.length})</h4>
      {shiftList.length > 0 && shiftList.map(shift => 
          <ShiftComponent shift={shift} user={user} deleteShift={deleteShift}/>
      )}
    </div>
  )
}